import Component from "vue-class-component";
import axios from "axios";
import moment from "moment";
import * as indexApi from "../../api/indexApi";

@Component({
  props: {},
  components: {
    // categoryView,
  }
})
export default class AddGoods extends Vue {
  date = [];
  model = false;
  item = 0
  handleInput2(e) {
            // 通过正则过滤小数点后两位
            e.target.value = (e.target.value.match(/^\d*(\.?\d{0,1})/g)[0]) || null
    
        }
  pickerOptions2 = {
    shortcuts: [
      {
        text: "最近一周",
        onClick(picker) {
          const end = new Date();
          const start = new Date();
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
          picker.$emit("pick", [start, end]);
        }
      },
      {
        text: "最近一个月",
        onClick(picker) {
          const end = new Date();
          const start = new Date();
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
          picker.$emit("pick", [start, end]);
        }
      },
      {
        text: "最近三个月",
        onClick(picker) {
          const end = new Date();
          const start = new Date();
          start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
          picker.$emit("pick", [start, end]);
        }
      }
    ]
  };
creatDoctorTime = 0
patientRegisterDate=0
preCreatTime=0
 preAuditing = 0
 loading= false
  doQuery() {
    if (!this.date || this.date.length !== 2) {
      this.$message("请选择时间");
      return;
    }
    let starttime = moment(this.date[0]).format("YYYY-MM-DD") + " 00:00:00";
    let endtime = moment(this.date[1]).format("YYYY-MM-DD") + " 23:59:59";
   this.loading = true

    indexApi
      .doQueryData({
        startTimeStr: starttime,
        endTimeStr: endtime
      })
      .then(res => {
    this.loading= false

        if (res["retCode"]) {
          this.model = true

        this.creatDoctorTime =res.data.creatDoctorTime
this.patientRegisterDate=res.data.patientRegisterDate
this.preCreatTime=res.data.preCreatTime
this.preAuditing = res.data.preAuditing

        } else {
          console.log(res)
          if(!res['islogin']){this.$alert(res["message"]);}
          console.error("数据查询错误");
        }
      });
  }

  mounted() {
    this.date = [new Date(),new Date()]
    this.doQuery()




// new Promise(resolve=>{


//     for(var i = 0; i < 10000; i++){
//         i == 99 && resolve();
//     }


// }).then(function(){
//     console.log('执行then函数啦')
// });



  }
}



